body{
  background-color: rgb(57, 55, 67);
}
.App {
  position: absolute;
  width: 50%;
  background-color: rgb(20, 20, 56);
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  border-radius: 20px;
  color: white;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
}

button{
  background-color: rgb(37, 155, 198);
  width: 150px;
  height: 50px;
  margin: 10px;
  border: none;
  border-radius: 10px;
  font-size: 24px;
  color: white;
}
input{
  margin: 10px;
  height: 30px;
  width: 150px;
  border: none;
  border-radius: 5px;
}
